<template>
  <div class="card">
    <div class="card-header text-dark chat-header" @click="toggleChat()">
      {{ chatHeader || 'unknown user' }}
      <span v-if="!open && unreadNo > 0" class="unread">{{ unreadMsg }}</span>
    </div>
    <div class="collapse" :class="{'show': open}">
      <div class="card-body">
        <div
          class="panel-body msg_container_base dialog-container"
          :class="{'hidden-chat-box': !open}" ref="chat-box">
          <div v-for="(m, index) in chat" :key="index">
            <div v-if="!m.is_client">
              <div class="row msg_container base_sent">
                <div class="text-container">
                  <div class="messages msg_sent">
                    <p>{{ m.message }}</p>
                    <time datetime="2009-11-13T20:00">
                      Me • {{ calcTime(m.time) }}
                    </time>
                  </div>
                </div>
                <div class="col-2 avatar">
                  <img src="../assets/employee-avatar.webp" class=" img-responsive ">
                </div>
              </div>
            </div>
            <div v-else>
              <div class="row msg_container base_receive">
                <div class="col-2 avatar">
                  <img src="../assets/user-avatar.webp" class=" img-responsive ">
                </div>
                <div class="text-container">
                  <div class="messages msg_receive">
                    <p>{{ m.message }}</p>
                    <time datetime="2009-11-13T20:00">
                      {{ m.user || m.user_info }} • {{ calcTime(m.time) }}
                    </time>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="panel-footer">
          <div class="input-group btn-input" :class="{'hidden-chat-box': !open}">
            <input type="text" v-model="userMessage"
              @keyup.enter="sendMessage()"
              class="form-control input-sm chat_input" ref="chat-input"
              placeholder="Write your message here..." autocomplete="off"/>
              <span class="input-group-btn">
              <button @click="sendMessage()"
                class="btn btn-dark btn-sm btn-chat">Send</button>
              </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex';

function addLeadingZero(inputNumber) {
  const result = inputNumber < 10 ? `0${inputNumber.toString()}` : inputNumber.toString();
  return result;
}

export default {
  name: 'ChatInstance',
  props: ['room'],
  data() {
    return {
      open: false,
      connection: null,
      chat: [],
      userMessage: null,
      username: '',
      userId: 0,
      clientId: this.$store.state.chat.clientId,
      unreadNo: 0,
      chatHeader: null,
    };
  },
  computed: {
    // chatHeader() {
    //   const deviceInfo = this.chat.length > 0 ?
    //    this.chat[this.chat.length - 1].user_info : 'Unkown User';
    //   const username = this.chat.length > 0 ? this.chat[0].user : '';
    //   return `${deviceInfo} * ${username}`;
    // },
    unreadMsg() {
      const result = this.open ? 0 : this.unreadNo;
      return result;
    },
  },
  created() {
  },
  async mounted() {
    this.$nextTick(() => {
      this.username = this.$store.state.user.userInfo.username;
      this.userId = this.$store.state.user.userInfo.id;
      this.clientId = this.$store.state.chat.clientId;
      this.chat = [];
      this.connectChat();
    });
  },
  methods: {
    ...mapActions('chat', ['getCSConversation']),
    getWebsocketLink() {
      console.log(this.room);
      const wsScheme = window.location.protocol === 'https:' ? 'wss' : 'ws';
      let wsHost = window.location.host;
      if (wsHost === '127.0.0.1:8080' || wsHost === '127.0.0.1:8000') {
        wsHost = '127.0.0.1:8000';
      } else {
        wsHost += ':8001';
      }
      const wsLink = `${wsScheme}://${wsHost}/ws/chat/${this.room}/admin/`;
      console.log(wsLink);
      return wsLink;
    },
    toggleChat() {
      this.open = !this.open;
      this.unreadNo = 0;
      this.scrollChat();
    },
    async connectChat() {
      this.unreadNo = 0;
      if (this.clientId === '0000') {
        await this.$store.dispatch('chat/getClientId').then(
          this.clientId = this.$store.state.chat.clientId,
        );
      }
      console.log(this.chat.length);
      if (this.chat.length === 0) {
        await this.getCSConversation(this.room)
          .then((response) => {
            this.chat = response;
            console.log('current chat: ', this.chat);
            this.chatHeader = `${this.chat[this.chat.length - 1].user_info} • ${this.chat[this.chat.length - 1].username}`;
          });
      }
      this.applyConnection();
    },
    sendMessage() {
      if (this.userMessage) {
        console.log(this.userMessage);
        const newMessage = {
          message: this.userMessage,
          user: this.username,
          user_id: this.userId,
          is_client: false,
        };
        this.connection.send(JSON.stringify(newMessage));
        this.userMessage = null;
      }
    },
    async applyConnection() {
      if (!this.connection) {
        // this.getConversation(this.clientId);
        await this.getCSConversation(this.room)
          .then((response) => {
            this.chat = response;
            console.log('current chat: ', this.chat);
          });
        this.connection = new WebSocket(this.getWebsocketLink());
        this.connection.onclose = (e) => {
          setTimeout(() => {
            console.log('connecting...');
            this.connection.reconnet(e);
          }, 1000);
        };
        this.connection.onopen = (event) => {
          console.log(event);
        };
        this.connection.onmessage = (event) => {
          const data = JSON.parse(event.data);
          console.log('chat message data: ', data);
          const newMessage = {
            message: data.message,
            room: this.clientId,
            time: data.time,
            user: data.user,
            is_client: data.is_client,
            user_info: data.user_info,
          };
          this.chat.push(newMessage);
          if (newMessage.is_client) {
            this.chatHeader = `${this.chat[this.chat.length - 1].user_info} • ${this.chat[this.chat.length - 1].user}`;
          }
          this.unreadNo += 1;
          this.scrollChat();
        };
      }
    },
    calcTime(chatTime) {
      const inputTime = new Date(chatTime);
      const currentTime = new Date();
      const timeDiff = Math.round((currentTime - inputTime) / 1000, 0);
      // console.log(inputTime);
      // console.log(timeDiff);
      let result = '';
      if (timeDiff < 30) {
        result = 'few sec.';
      } else if (timeDiff < 60) {
        result = `${timeDiff} sec`;
      } else if (timeDiff < 3600) {
        result = `${Math.floor(timeDiff / 60)} min`;
      } else if (timeDiff < 86400) {
        result = `${Math.floor(timeDiff / 60 / 60)} hrs`;
      } else {
        const monthNames = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'];
        const timeDay = addLeadingZero(inputTime.getDate());
        const timeHour = addLeadingZero(inputTime.getHours());
        const timeMinute = addLeadingZero(inputTime.getMinutes());
        result = `${timeDay}${monthNames[inputTime.getMonth()]} ${timeHour}:${timeMinute}`;
      }
      return result;
    },
    scrollChat() {
      this.$nextTick(() => {
        const chatBox = this.$refs['chat-box'];
        if (chatBox) {
          chatBox.scroll(0, chatBox.scrollHeight);
        }
      });
    },
  },
  watch: {
    open() {
      this.$nextTick(() => {
        this.scrollChat();
      });
    },
  },
};
</script>
<style scoped>
.col-2 {
    padding:0;
}

.text-container {
  width: calc(100% - 60px);
}

.col-2 {
  flex: 0 0 0 60px;
  max-width: 60px;
}

.panel{
    margin-bottom: 0px;
}
.chat-window{
    bottom:0;
    position:fixed;
    float:right;
    margin-left:10px;
}
.chat-window > div > .panel{
    border-radius: 5px 5px 0 0;
}
.icon_minim{
    padding:2px 10px;
}
.msg_container_base{
  background: #e5e5e5;
  margin: 0;
  padding: 0 10px 10px;
  max-height:300px;
  min-height: 300px;
  overflow-x:hidden;
  -webkit-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  -moz-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);

}
.top-bar {
  background: #666;
  color: white;
  padding: 10px 10px 5px 10px;
  position: relative;
  overflow: hidden;
}
.msg_receive{
    padding-left:0;
    margin-left:0;
}
.msg_sent{
    padding-bottom:20px !important;
    margin-right:0;
}
.messages {
  background: white;
  padding: 10px;
  border-radius: 2px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
  max-width:100%;
}
.messages > p {
    font-size: 14px;
    margin: 0 0 0.2rem 0;
  }
.messages > time {
    font-size: 11px;
    color: #ccc;
}
.msg_container {
    padding: 10px;
    overflow: hidden;
    display: flex;
}
img {
    display: block;
    width: 50px;
    height: 50px;
}
.avatar {
    position: relative;
}
.base_receive > .avatar:after {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    width: 0;
    height: 0;
    border: 5px solid #FFF;
    border-left-color: rgba(0, 0, 0, 0);
    border-bottom-color: rgba(0, 0, 0, 0);
}

.base_sent {
  justify-content: flex-end;
  align-items: flex-end;
}
.base_sent > .avatar:after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 0;
    height: 0;
    border: 5px solid white;
    border-right-color: transparent;
    border-top-color: transparent;
    box-shadow: 1px 1px 2px rgba(black, 0.2);
}

.msg_sent > time {
    float: right;
}

.msg_container_base::-webkit-scrollbar-track
{
  box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
    background-color: #F5F5F5;
}

.msg_container_base::-webkit-scrollbar
{
    width: 12px;
    background-color: #F5F5F5;
}

.msg_container_base::-webkit-scrollbar-thumb
{
    box-shadow: inset 0 0 6px rgba(0,0,0,.3);
    background-color: #555;
}

.btn-group.dropup{
    position:fixed;
    left:0px;
    bottom:0;
}

#chat_window_1 {
  right: 10px;
  min-width: 315px;
  max-width: 350px;
  z-index: 900;
}

.panel-heading {
  border-radius: 10px 10px 0 0;
  cursor: pointer;
  -webkit-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  -moz-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  margin-bottom: 0;
}

#chat-logo {
  width: 30px;
}

.chat-logo-container {
  float: right;
}

.hidden-chat-box {
  min-height: 0 !important;
  height: 0 !important;
  padding: 0 !important;
}

#chat-caption {
  display: inline-block;
  text-align: center;
  width: 80%;
  margin: 2px 0 -4px 0;
}

#btn-input {
  -webkit-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  -moz-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  padding: 20px;
  font-size: 15px;
}

.btn-chat {
  height: 33px;
  margin-top: 1px;
  margin-bottom: 1px;
  -webkit-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  -moz-box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
  box-shadow: -5px -5px 23px -8px rgba(10,10,10,1);
}

#chat-header {
  margin-bottom: 0;
}

.card-header {
  cursor: pointer !important;
}

.unread {
  background-color: grey;
  color: white;
  border: 1px solid grey;
  padding: 2px 9px;
  border-radius: 50%;
  float: right;
}
</style>
